<template>
  <el-drawer
    :center="true"
    :append-to-body="true"
    :close-on-press-escape="false"
    :wrapper-closable="false"
    :title="title"
    size="50"
    :visible="value"
    :destroy-on-close="true"
    :before-close="beforeClose"
    @open="initSelect"
  >
    <el-form ref="form" :model="form" inline :rules="rules" label-width="120px">
      <el-scrollbar class="scroll-drawer">
        <el-form-item label="仓库名称" prop="name">
          <el-input v-model.trim="form.name" placeholder="请输入" style="width: 178px;" />
        </el-form-item>
        <el-form-item label="工厂" prop="factoryId">
          <el-select v-model="form.factoryId" clearable placeholder="请选择" style="width: 178px;" class="filter-item">
            <el-option
              v-for="item in factoryPullList"
              :key="item.id"
              :label="item.name"
              :value="item.id"
            />
          </el-select>
        </el-form-item>
        <el-form-item label="备注" prop="remarks" class="remarks-sty">
          <el-input v-model.trim="form.remarks" placeholder="请填写" type="textarea" :rows="5" />
        </el-form-item>
      </el-scrollbar>
      <div class="drawer-footer">
        <el-button :loading="loading" type="primary" @click="handleConfirm('form')">{{ loading ? '提交中 ...' : '确 定' }}</el-button>
        <el-button @click="beforeClose">取 消</el-button>
      </div>
    </el-form>
  </el-drawer>
</template>

<script>
import { getAddApi as addApi, getEditApi as updataApi } from '@/apis/modules/base/baseSapWarehouse'
import { getPullListItemApi as getFactoryPullList } from '@/apis/modules/base/baseFactory'

const INIT_FORM = () => ({
  name: '',
  factoryId: '',
  remarks: ''
})

export default {
  props: {
    value: {
      type: Boolean,
      default: false
    },
    title: {
      type: String,
      default: ''
    },
    editType: {
      type: String,
      default: 'add'
    },
    dialogForm: {
      type: Object,
      default: () => {
        return { ...INIT_FORM() }
      }
    }
  },
  data() {
    return {
      form: INIT_FORM(),
      loading: false,
      rules: {
        name: [
          { required: true, message: '请输入仓库名称', trigger: 'blur' }
        ],
        factoryId: [
          { required: true, message: '请选择工厂', trigger: 'blur' }
        ],
        remarks: [
          { required: true, message: '请填写备注', trigger: 'blur' }
        ]
      },
      gradeOpts: [],
      factoryPullList: []
    }
  },
  computed: {
  },
  // 所属车间类别 所属车间名称
  dicts: [],
  watch: {
    dialogForm: {
      handler: function(value) {
        if (this.editType === 'add') {
          this.form = { ...INIT_FORM() }
        } else {
          // 进行回显
          this.form = { ...value }
        }
      },
      immediate: true
    }
  },
  methods: {
    handleConfirm(formName) {
      this.$refs[formName].validate((valid) => {
        if (valid) {
          this.loading = false
          const api = this.editType === 'modify' ? updataApi : addApi
          api({
            ...this.form
          }).then(res => {
            const { code } = res
            if (!code) {
              this.$message({
                message: `${this.title}成功`,
                type: 'success'
              })
              this.$emit('confirm')
              this.beforeClose()
            }
            this.loading = false
          }).catch(err => {
            console.log(err)
            this.loading = false
          })
        } else {
          console.log('error submit!!')
          return false
        }
      })
    },
    initSelect() {
      this.getFactoryPullList()
    },
    // 下拉数据
    getFactoryPullList() {
      return new Promise((resolve, reject) => {
        getFactoryPullList({}).then(res => {
          const { code, data } = res
          if (!code) {
            this.factoryPullList = data || []
          }
          resolve(res)
        }).catch(err => {
          reject(err)
        })
      })
    },
    beforeClose(done) {
      this.$refs.form.resetFields()
      this.$emit('input', false)
      this.form = Object.assign({}, { ...INIT_FORM() })
      typeof done === 'function' && done()
    },
    handleChange(val) {
      console.log(val)
    }
  }
}
</script>

<style lang='scss' scoped>
.container{
  width: 860px;
  margin: 0 auto;
  .left{
    width: 640px;
  }
  .right{
    margin-left: 20px;
    width: 180px;
  }
}
::v-deep:focus {
  outline: 0;
}
.scroll-drawer {
  height: calc(100vh - 150px);
  // overflow: auto;
}
::v-deep .el-scrollbar .el-scrollbar__wrap {
  overflow-x: hidden;
}
::v-deep .el-drawer {
  width: 640px;
}
.drawer-footer{
  text-align: right;
}
::v-deep .remarks-sty {
  margin: 0;
  width: 100%;
  .el-form-item__content {
    width: calc(100% - 125px);
  }
}
</style>

